import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Grid,
  Avatar,
  Chip,
  Button,
  CircularProgress,
  Alert,
  Divider,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Tabs,
  Tab,
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Phone as PhoneIcon,
  Email as EmailIcon,
  LocationOn as LocationIcon,
  Pets as PetsIcon,
  Cake as CakeIcon,
  Scale as ScaleIcon,
  Notes as NotesIcon,
  CalendarToday as CalendarIcon,
  PhotoCamera as PhotoCameraIcon,
  Person as PersonIcon,
} from '@mui/icons-material';
import { CustomerData, PetPhoto } from '../types/customer';
import apiService from '../services/apiService';
import PhotoService from '../services/photoService';
import PhotoUpload from './PhotoUpload';
import PhotoGallery from './PhotoGallery';
import CustomerEditForm from './CustomerEditForm';

const CustomerDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [customer, setCustomer] = useState<CustomerData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [photos, setPhotos] = useState<PetPhoto[]>([]);
  const [activeTab, setActiveTab] = useState(0);
  const [uploadDialogOpen, setUploadDialogOpen] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [updateLoading, setUpdateLoading] = useState(false);

  const photoService = new PhotoService();


  const loadCustomer = useCallback(async (customerId: string) => {
    try {
      setLoading(true);
      setError(null);
      
      // API
      const data = await apiService.getCustomerById(customerId);
      
      if (data) {
        setCustomer(data);
      } else {
        setError('');
      }
    } catch (err) {
      setError('');
      console.error('Error loading customer:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (id) {
      loadCustomer(id);
      // 
      const customerPhotos = photoService.getPhotosByCustomerId(id);
      setPhotos(customerPhotos);
    }
  }, [id, loadCustomer, photoService]);

  const handlePhotoUploaded = (photo: PetPhoto) => {
    setPhotos(prev => [...prev, photo]);
  };

  const handlePhotoDeleted = (photoId: string) => {
    setPhotos(prev => prev.filter(photo => photo.id !== photoId));
  };

  const handlePhotoUpdated = (updatedPhoto: PetPhoto) => {
    setPhotos(prev => prev.map(photo => 
      photo.id === updatedPhoto.id ? updatedPhoto : photo
    ));
  };

  // 
  const handleUpdateCustomer = async (updatedCustomer: CustomerData) => {
    try {
      setUpdateLoading(true);
      
      // API
      await apiService.updateCustomer(updatedCustomer.id, updatedCustomer);
      
      // 
      setCustomer(updatedCustomer);
      setIsEditing(false);
      
      console.log('');
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setUpdateLoading(false);
    }
  };

  // 
  const handleCancelEdit = () => {
    setIsEditing(false);
  };

  const getPetTypeColor = (petType: string) => {
    switch (petType) {
      case '': return 'primary';
      case '': return 'secondary';
      default: return 'default';
    }
  };

  const getStatusColor = (status: string) => {
    return status === 'active' ? 'success' : 'default';
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
        <CircularProgress />
      </Box>
    );
  }

  if (error || !customer) {
    return (
      <Box>
        <Button
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/')}
          sx={{ mb: 2 }}
        >
          
        </Button>
        <Alert severity="error">
          {error || ''}
        </Alert>
      </Box>
    );
  }

  return (
    <Box>
      {/*  */}
      <Box sx={{ mb: 3, display: 'flex', alignItems: 'center', gap: 2 }}>
        <Button
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/')}
          variant="outlined"
        >
          
        </Button>
        <Button
          variant="contained"
          onClick={() => setIsEditing(true)}
          disabled={isEditing}
        >
          
        </Button>
        <Typography variant="h4" sx={{ flexGrow: 1 }}>
          
        </Typography>
        <Button
          startIcon={<PhotoCameraIcon />}
          variant="contained"
          color="primary"
          onClick={() => setUploadDialogOpen(true)}
        >
          
        </Button>
      </Box>

      {/*  */}
      {isEditing && customer && (
        <Box sx={{ mb: 3 }}>
          <CustomerEditForm
            customer={customer}
            onSave={handleUpdateCustomer}
            onCancel={handleCancelEdit}
            loading={updateLoading}
          />
        </Box>
      )}

      {/*  */}
      {!isEditing && (
        <Grid container spacing={3}>
        {/*  */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mb: 3 }}>
                <Avatar
                  src={customer.imageUrl}
                  alt={customer.petName}
                  sx={{ width: 120, height: 120, mb: 2 }}
                >
                  {customer.petName.charAt(0)}
                </Avatar>
                <Typography variant="h5" gutterBottom>
                  {customer.petName}
                </Typography>
                <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
                  <Chip
                    label={customer.petType}
                    color={getPetTypeColor(customer.petType)}
                    size="small"
                  />
                  <Chip
                    label={customer.status === 'active' ? '' : ''}
                    color={getStatusColor(customer.status)}
                    size="small"
                  />
                </Box>
              </Box>

              <Divider sx={{ my: 2 }} />

              <List dense>
                <ListItem>
                  <ListItemIcon>
                    <PetsIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={customer.breed}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <CakeIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={`${customer.age}`}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <ScaleIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={`${customer.weight}kg`}
                  />
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Grid>

        {/*  */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Divider sx={{ mb: 2 }} />
              
              <List dense>
                <ListItem>
                  <ListItemText
                    primary=""
                    secondary={customer.name}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <PhoneIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={customer.phone}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <EmailIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={customer.email}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <LocationIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={customer.address}
                  />
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Grid>

        {/*  */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <Divider sx={{ mb: 2 }} />
              
              <List dense>
                <ListItem>
                  <ListItemIcon>
                    <CalendarIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={new Date(customer.createdAt).toLocaleDateString('ja-JP')}
                  />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <CalendarIcon />
                  </ListItemIcon>
                  <ListItemText
                    primary=""
                    secondary={customer.lastVisit ? new Date(customer.lastVisit).toLocaleDateString('ja-JP') : ''}
                  />
                </ListItem>
              </List>

              {customer.notes && (
                <>
                  <Divider sx={{ my: 2 }} />
                  <Box>
                    <Typography variant="subtitle2" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <NotesIcon />
                      
                    </Typography>
                    <Paper sx={{ p: 2, backgroundColor: 'grey.50' }}>
                      <Typography variant="body2" sx={{ whiteSpace: 'pre-wrap' }}>
                        {customer.notes}
                      </Typography>
                    </Paper>
                  </Box>
                </>
              )}
            </CardContent>
          </Card>
        </Grid>

        {/*  */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Tabs
                value={activeTab}
                onChange={(_, newValue) => setActiveTab(newValue)}
                sx={{ mb: 2 }}
              >
                <Tab
                  icon={<PersonIcon />}
                  label=""
                  iconPosition="start"
                />
                <Tab
                  icon={<PhotoCameraIcon />}
                  label={` (${photos.length})`}
                  iconPosition="start"
                />
              </Tabs>

              {activeTab === 0 && (
                <Box>
                  <Typography variant="h6" gutterBottom>
                    
                  </Typography>
                  <Divider sx={{ mb: 2 }} />
                  <Typography color="text.secondary">
                    
                  </Typography>
                </Box>
              )}

              {activeTab === 1 && customer && (
                <PhotoGallery
                  photos={photos}
                  customerId={customer.id}
                  onPhotoDeleted={handlePhotoDeleted}
                  onPhotoUpdated={handlePhotoUpdated}
                />
              )}
            </CardContent>
          </Card>
        </Grid>
      </Grid>
      )}

      {/*  */}
      {customer && (
        <PhotoUpload
          open={uploadDialogOpen}
          onClose={() => setUploadDialogOpen(false)}
          customerId={customer.id}
          customerName={customer.name}
          petName={customer.petName}
          onPhotoUploaded={handlePhotoUploaded}
        />
      )}
    </Box>
  );
};

export default CustomerDetail;
